body {
    background: #3498db;
    font-family: sans-serif;
}

p {
    position: relative;
    color: rgba(0, 0, 0, .3);
    font-size: 2em
}

p:before {
    content: attr(data-text);
    position: absolute;
    overflow: hidden;
    white-space: nowrap;
    max-width: 800px;
    color: #fff;
    animation: loading 2s linear;
}

@keyframes loading {
    0% {
        max-width: 0;
    }
}